// 변수 선언하여 그 값을 사용
$imageWidth: 150px; // 하단 이미지 썸네일의 너비
$imageHeight: 150px; // 하단 이미지 썸네일의 너비

.toastUIContainer {
  margin: auto;
  width: 100%;
  height: 100%;
  border: 1px solid silver;
  border-radius: 10px;
  overflow: hidden;

  .leftWrapper {
    float: left;
    width: 65%;
    height: 100%;
    border-right: 1px solid silver;

    .bodyImageLayout {
      width: 100%;
      height: 83%;
      padding: 2rem;
      background-color: black;
      color: white;

      .bodyImage {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }

    .ImagePreviewLayout {
      width: 100%;
      height: 17%;

      .imageWrapper {
        float: left;
        width: 100%;
        height: 100%;
      }

      .imagePreview {
        white-space: nowrap; /* 이미지들이 가로로 넘치지 않도록 설정 */
        overflow-x: auto; /* 가로 스크롤을 제공하여 넘친 이미지들 볼 수 있도록 설정 */
        overflow-y: hidden; /* 세로 스크롤은 숨김 처리 */
        width: 100%;
        height: 100%;

        .uploadedImage {
          display: inline-block; /* 가로로 배열되도록 설정 */
          height: 100%;
          width: $imageWidth;
          position: relative;

          .closeBtn {
            position: absolute;
            top: 5px;
            right: 5px;
            width: 22px;
            height: 22px;
            cursor: pointer;
          }

          .imageLayout {
            float: left;
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            border-right: 1px solid silver;

            img {
              width: 80%;
              height: 80%;
              object-fit: contain;
            }
          }
        }
        label {
          margin-right: 10px;
          height: 100%;
          width: $imageWidth;
          text-align: center;
          vertical-align: top; /* 상단으로 정렬 */

          .labelText {
            width: 100%;
            height: 100%;
            border-left: 1px solid silver;
            border-right: 1px solid silver;
            border-radius: 5px;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 12px;
            cursor: pointer;
          }
        }
      }
    }
  }

  .rightWrapper {
    float: left;
    width: 35%;
    height: 100%;

    .InfoLayout {
      width: 100%;
      height: 100%;

      .PostInfoLayout {
        width: 100%;
        height: 40%;
        position: relative;

        .post {
          width: 100%;
          height: 85%;
          border: 0px;
          font-size: 14px;
          padding: 10px;
          resize: none;
        }
        .inputCount{
          position:absolute;
          right:0;
          padding-right:8px;
          height:15%;
          line-height:36px;
          font-size:15px;
        }
      }

      .hashLayout {
        width: 100%;
        margin-bottom: 1rem;
            .Select{
                width: 100%;
                height: 100%;
                *{
                    max-height: 100%;
                }
            }
        }

      .weatherSelectLayout {
        width: 100%;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        .select1 {
          width: 65%;
        }
        .select2 {
          width: 35%;
        }
      }

      .dateLayout {
        width: 100%;
        height: 5%;
        input {
          margin: 0px;
          width: 100%;
          height: 100%;
        }
      }

      .weatherResultLayout {
        width: 100%;
        height: 25%;
        display: block;
        justify-content: center;
        align-items: center;
        .todayName {
            font-size: 15px;
          text-align: center;
        }
        .todaySky {
            margin-top: 30px;
            margin-bottom: 10px;
          text-align: center;
          height: 40%;
          svg {
            height: 100%;
            filter:drop-shadow(3px 3px 3px rgb(197, 197, 197));
          }
        }
        .todayTemp {
          position: relative;
          margin-top: 25px;
          text-align: center;
          .maxTemp{
            font-size: larger;
            color: red;
          }
          .minTemp{
            margin-top: 10px;
            font-size: small;
            color: rgb(0, 0, 116);
          }
        }
      }

      .messageLayout {
        width: 100%;
        height: 5%;
        text-align: center;
        font-size: 14px;
        color: silver;
      }

      .btnWrapper {
        margin-top: 6rem;
        width: 100%;
        height: 10%;
        .btnLayout {
          width: 100%;
          height: 20%;
          display: flex;
          justify-content: space-evenly;

          button {
            border: none;
            border-radius: 20px;
            width: 100px;
            height: 40px;
            background-color: black;
            color: white;
            cursor: pointer;
            font-size: 16px;
          }
        }
      }
    }
  }
}
