%borderBox {
  height: 34px;
  width: 42px;
  border: 1px solid #c9cdd2;
  cursor: pointer;
  transform: none;
  transition: all 0s ease 0s;
  background-color: #ffffff;
  color: #72787f;
  line-height: 18.2px;
  text-decoration: none solid rgb(114, 120, 127);
  text-align: center;
  vertical-align: middle;
  word-spacing: 0px;
}

.reportmodalwrapper * {
  box-sizing: border-box;
}

.reportmodalwrapper {
  height: 100%;
  display: flex;
  align-content: center;
  justify-content: center;

  .modalWrapper {
    height: 100%;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 101;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.5);

    .reportInnerWrapper {
      display: flex;
      width: 500px;

      background: #ffffff;
      color: #454c53;
      padding: 0;
      margin-top: 0;
      border: 0px solid #e8ebed;
      border-radius: 6px;

      .closeBtnLayout {
        justify-content: right;
        padding: 5px 5px 0px 0px;
        display: flex;
        .closeBtn {
          cursor: pointer;
          z-index: 1;
          width: 30px;
          height: 30px;
          border-radius: 30px;
          background-color: white;
          border-color: silver;
        }
      }

      .radioLayout {
        width: 60%;
        margin: auto;
        margin-bottom: 20px;
        .radios {
          label {
            cursor: pointer;
            display: block;
            text-align: left;
          }
        }
      }
      .bottomLayout {
        height: 110px;
        margin-top: 20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
    }
  }
}
