%borderBox {
  height: 34px;
  width: 42px;
  border: 1px solid #c9cdd2;
  cursor: pointer;
  transform: none;
  transition: all 0s ease 0s;
  background-color: #ffffff;
  color: #72787f;
  line-height: 18.2px;
  text-decoration: none solid rgb(114, 120, 127);
  text-align: center;
  vertical-align: middle;
  word-spacing: 0px;
}

.wrapper * {
  box-sizing: border-box;
}

.wrapper {
  height: 100%;
  display: flex;
  align-content: center;
  justify-content: center;

  .modalWrapper {
    height: 100%;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 101;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.5);

    .innerWrapper {
      display: flex;
      width: 1200px;
      height: 90vh;

      background: #ffffff;
      color: #454c53;
      padding: 0;
      margin-top: 0;
      border: 0px solid #e8ebed;
      border-radius: 6px;

      .leftWrapper {
        width: 65%;
        height: 100%;
        border-right: 1px solid #e8ebed;

        .imgWrapper {
          width: 100%;
          height: 70%;
          background-color: black;
          .slick-slider {
            max-width: 100%;
            height: 100%;

            .slick-prev {
              z-index: 3;
              left: 25px;
            }
            .slick-next {
              z-index: 3;
              right: 25px;
            }
            .slick-list {
              max-width: 100%;
              height: 100%;

              .slick-track {
                // max-width: 100%;
                height: 100%;
                .slick-slide {
                  max-width: 100%;
                  height: 100%;
                  div {
                    max-width: 100%;
                    height: 100%;
                    div {
                      max-width: 100%;
                      height: 100%;
                    }
                  }
                }
              }
            }
          }
        }

        .smallImages {
          display: flex;
          justify-content: center;
          width: 600px;
          height: 78px;
          padding: 15px 0 15px 0;

          .displayNone {
            display: none;
          }

          .smallImagesWrapper {
            width: 48px;
            height: 48px;
            margin-left: 6px;
            overflow: hidden;
          }

          .smallImage {
            width: 44px;
            height: 44px;
            border: 0px solid #e8ebed;
            border-radius: 6px;
          }
        }
      }

      .information {
        width: 100%;
        height: 30%;
        border: 1px solid #e8ebed;
        border-radius: 6px;
        font-size: 16px;
        line-height: 20.8px;
        text-align: left;

        .commentData {
          display: flex;
          position: relative;
          height: 26%;
          width: 100%;
          padding: 10px;

          .commentUserImgWrapper {
            width: 7%;
            // height: 40px;
            margin-right: 8px;
            margin-left: 8px;

            .commentUserImg {
              border: 0px solid #ffffff;
              border-radius: 50%;
              cursor: pointer;
            }
          }
          .authorInformation {
            width: 600px;
            // height: 33px;
            // display: inline;
            font-size: 14px;
            // font-weight: 700;
            line-height: 18.2px;
            // text-align: left;
            white-space: nowrap;
            word-spacing: 0px;
            overflow: hidden;
            margin-right: 10px;
            padding-top: 5px;
          }

          .author {
            margin: 0 4px 0 0;
            // max-width: 170px;
            padding-top: 5px;
            margin-bottom: 2px;
            font-size: 14px;
            font-weight: 700;
            line-height: 18.2px;
            text-decoration: none solid rgb(69, 76, 83);
            text-align: left;
            white-space: nowrap;
            word-spacing: 0px;
            cursor: pointer;
          }

          .introduction {
            font-size: 12px;
            // max-width: 190px;
            color: #9ea4aa;
            overflow: hidden;
            font-size: 13px;
            line-height: 15.6px;
            text-decoration: none solid rgb(158, 164, 170);
            text-align: left;
            white-space: nowrap;
            word-spacing: 0px;
          }
          .optionBox {
            @extend %borderBox;
            border: 0px solid white;
            margin-left: auto;

            .option {
              width: 100%;
              height: 70%;
              margin-top: 12px;
            }
            .option:hover {
              opacity: 50%;
            }
          }
          .optionList {
            z-index: 1;
            position: absolute;
            right: 0;
            top: 50px;
            border: 1px solid silver;
            width: 90px;
            text-align: center;
            background-color: white;
            .optionListDiv {
              font-size: 12px;
              height: 37px;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              a {
                cursor: pointer;
              }
              a:hover {
                color: #ff0066;
              }
            }
          }
          // .followBtnBox {
          //   border: 1px solid silver;
          //   border-radius: 6px;
          //   margin-top:8px;
          //   width: 36px;
          //   height:36px;
          //   margin-left: auto;
          //   text-align: center;

          //   .followBtn {
          //     height: 70%;
          //     width: 70%;
          //     color: #ff5f00;
          //     cursor: pointer;
          //     margin-top: 5px;
          //     margin-left: 2px;
          //   }

          //   .followBtnActive {
          //     height: 70%;
          //     width: 70%;
          //     color: #ffffff;
          //     background-color: #ff5f00;
          //     cursor: pointer;
          //     margin-top: 5px;
          //     margin-left: 2px;
          //   }
          // }
        }
        .authorDescription {
          height: 50%;
          font-size: 14px;
          display: flex;
          // line-height: 16.9px;
          text-decoration: none solid rgb(38, 40, 43);
          text-align: left;
          white-space: pre-wrap;
          word-spacing: 0px;
          padding-top: 10px;
          padding-bottom: 10px;
          margin-left: 30px;

          .feedPostBody {
            width: 100%;
            .post {
              height: 100%;
              width: 100%;
              resize: none;
              border: 1px solid hsl(0, 0%, 80%);
            }
            overflow: hidden;
          }

          .feedPostBodyRight {
            width: 20%;
            .feedPostBodyRightTop {
              display: flex;
              height: 50%;
              .feedPostWeather {
                width: 80%;
                display: flex;
                text-align: center;
                align-items: center;
                .weatherIcon {
                  width: 50%;
                  svg {
                    width: 100%;
                    height: 100%;
                    filter:drop-shadow(3px 3px 3px rgb(197, 197, 197));
                  }
                }
                .writeTemp {
                  width: 50%;
                  font-size: 18px;
                  margin-left: 5px;
                }
              }
              .feedPostWidth {
                width: 10%;
              }
            }

            .feedPostBodyRightBottom{
              height: 50%;
              .inputCount{
                display: flex;
                align-items: center;
                justify-content: center;
                height: 100%;
              text-align: center;
                font-size: 15px;
              }
            }
            
          }
        }

        .hashTagBody {
          display: flex;
          height: 24%;
          margin-left: 30px;
          padding-bottom: 10px;
          line-height: 50px;
          font-size: 12px;
          .selectLayout {
            width: 70%;
            height: 100%;
            .crSelect {
              width: 100%;
              display: flex;
              justify-content: center;
              align-items: center;
            }
          }
          .buttons {
            width: 30%;
            height: 100%;
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            transition-duration: 0.3s;
            

            button {
              height: 30px;
              background-color: black;
              border-radius: 20px;
              border: 1px solid black;
              color: white;
              cursor: pointer;
              transition-duration: 0.3s;
            }
            button:hover{
              transform: scale(1.1);
            }
            button:active{
              transform: scale(0.9);
            }
          }
        }
      }
    }
    .rightWrapper {
      width: 35%;
      justify-content: center;
      padding: 20px 15px 15px 20px;
      overflow-y: auto;
      word-break: break-all;

      .authorPopularity {
        width: 100%;
        display: flex;
        padding-top: 15px;
        padding-bottom: 15px;
        border-bottom: 1px solid #c9cdd2;

        .btnLayout {
          display: flex;
          justify-content: center;
          transition: transform 0.2s ease-in-out;
        }
        .likeBox {
          @extend %borderBox;
          border-top-left-radius: 6px;
          border-bottom-left-radius: 6px;
          color: #ff0066;
          background-color: white;
        }
        .disLikeBox {
          @extend %borderBox;
          border-top-left-radius: 6px;
          border-bottom-left-radius: 6px;
        }

        .like {
          width: 83%;
          height: 83%;
          padding-top: 6px;
        }

        .likeNumBox {
          @extend %borderBox;
          border-top-right-radius: 6px;
          border-bottom-right-radius: 6px;
          font-size: 15px;
          font-weight: 600;
          padding-top: 5px;
          color: silver;
          text-align: center;
          // background-color: lightcoral;
        }

        // .dislikeNumBox {
        //   @extend %borderBox;
        //   border-top-right-radius: 6px;
        //   border-bottom-right-radius: 6px;
        //   font-size: 18px;
        //   font-weight: 700;
        //   padding-top: 5px;
        // }

        .scrapBox {
          @extend %borderBox;
          border-radius: 6px;
          margin-left: 8px;
          color: #ff0066;
          background-color: white;
          padding-top: 3px;
        }

        .disScrapBox {
          @extend %borderBox;
          border-radius: 6px;
          margin-left: 8px;
          background-color: white;
          padding-top: 3px;
        }
        .scrapImage {
          width: 93%;
          height: 93%;
          padding-top: 3px;
        }
      }

      .commentWrapper {
        width: 95%;
        overflow: hidden;
        max-height: 533px;
        margin-bottom: 5px;

        .commentBox {
          display: flex;
          overflow: hidden;
        }
        .imgWrapper {
          display: flex;
          width: 30px;
          height: 30px;

          .commentUserImg {
            width: 80%;
            height: 80%;
            border: 0px solid #ffffff;
            border-radius: 50%;
            cursor: pointer;
            transform: none;
            transition: all 0s ease 0s;
          }
          .commentBtn {
            margin-bottom: 10px;
          }
        }

        .commentInfoWrapper {
          margin-left: 10 px;
          font-size: 16px;
          line-height: 20.8px;
          text-decoration: none solid rgb(38, 40, 43);
          text-align: left;
          word-spacing: 0px;
          background-color: #ffffff;
          background-position: 0% 0%;
          color: #26282b;
          height: auto;
          width: 275px;
          overflow: hidden;
          transform: none;
          transition: all 0s ease 0s;

          .commentContentWrapper {
            font-size: 12px;
            line-height: 15px;
            white-space: pre-wrap;
            color: #26282b;

            .commentUser {
              font-weight: 700;
            }

            .commentContent {
              padding-left: 5px;
            }
          }

          .commentBtnWrapper {
            font-size: 12px;
            line-height: 15.6px;
            text-decoration: none solid rgb(158, 164, 170);
            text-align: left;
            word-spacing: 0px;
            background-color: #ffffff;
            background-position: 0% 0%;
            color: #9ea4aa;
            height: auto;
            width: auto;
            margin-bottom: 7px;

            .commentBtn {
              cursor: pointer;
              padding-left: 8px;
            }
          }
        }

        .commentCreateBox {
          display: flex;
          overflow: hidden;
        }

        .commentCreateImgWrapper {
          display: flex;
          width: 30px;
          height: 30px;

          .commentCreateUserImg {
            width: 100%;
            height: 100%;
            border: 0px solid #ffffff;
            border-radius: 50%;
            cursor: pointer;
            transform: none;
            transition: all 0s ease 0s;
          }
        }

        .commentCreateInfoWrapper {
          font-size: 16px;
          line-height: 20.8px;
          text-decoration: none solid rgb(38, 40, 43);
          text-align: left;
          word-spacing: 0px;
          background-color: #ffffff;
          background-position: 0% 0%;
          color: #26282b;
          height: 36px;
          width: 275px;
          overflow: hidden;
          transform: none;
          transition: all 0s ease 0s;

          .commentCreateContentWrapper {
            font-size: 12px;
            line-height: 15px;
            white-space: pre-wrap;
            color: #26282b;

            .commentCreateUser {
              font-weight: 700;
            }

            .commentCreateContent {
              padding-left: 5px;
            }
          }

          .commentCreateBtnWrapper {
            font-size: 12px;
            line-height: 15.6px;
            text-decoration: none solid rgb(158, 164, 170);
            text-align: left;
            word-spacing: 0px;
            background-color: #ffffff;
            background-position: 0% 0%;
            color: #9ea4aa;
            height: auto;
            width: auto;

            .commentCreateBtn {
              cursor: pointer;
              padding-left: 8px;
            }
          }
        }
      }

      .commentNum {
        width: 100%;
        margin: 10px 0 10px 0;
        font-size: 15px;
        font-weight: 700;
        line-height: 18.2px;
        text-decoration: none solid rgb(114, 120, 127);
        text-align: left;
        word-spacing: 0px;
      }

      .inputWrapper {
        width: 100%;
        border-top: 1px solid #c9cdd2;
        font-size: 22px;
        line-height: 16px;
        text-decoration: none solid rgb(38, 40, 43);
        text-align: left;
        word-spacing: 0px;
        color: #26282b;
        cursor: default;
        margin-top: 10px;

        .inputReple {
          width: 100%;
          height: 43px;
          border: 1px solid #c9cdd2;
          border-radius: 6px;
          padding: 10px 10px 10px 10px;
          margin-top: 10px;
        }
      }

      .reple {
        border: 1px solid #c9cdd2;
        border-radius: 6px;
        margin-top: 10px;
        padding: 10px;

        .repleBox1 {
          display: flex;

          .repleProfile {
            img {
              border-radius: 50%;
              border: 1px solid silver;
              width: 30px;
              height: 30px;
              margin-right: 10px;
            }
          }
          .repleNickname {
            font-size: 13px;
            font-weight: bold;
            margin-bottom: 7px;
          }
          .likeRepleBox {
            margin-left: auto;
            svg {
              fill: #ff0066;
            }
          }
          .dislikeRepleBox {
            margin-left: auto;
          }
        }
        .repleWrite {
          font-size: 12px;
        }
      }
    }
  }
}
