body {
  // font-family: Ariel, sans-serif;
  background-color: #fafafa;
  margin: 0;
  padding: 0;
  color: #000000;
}

.myPickPageLayout {
  .profile {
    width: 1100px;
    margin: 40px auto;
    padding: 20px;
    background-color: #fff;

    .profileHeader {
      width: 100%;
      height: 200px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-bottom: 1px solid rgb(222, 222, 222);

      .imageLayout {
        width: 230px;
        display: flex;
        justify-content: center;
        align-items: center;

        .shortCutMenu {
          display: none;
        }

        img {
          width: 150px;
          height: 150px;
          border-radius: 50%;
          object-fit: cover;
          border: 1px solid silver;
        }
      }

      .profileInfo {
        width: 25%;
        h1 {
          font-size: 25px;
          margin-bottom: 2px;
        }
        .id {
          font-size: 20px;
          font-weight: 500;
          margin-bottom: 10px;
        }
        .bio {
          font-size: 16px;
          padding: 1%;
        }
        .hashTag {
          font-size: 14px;
          padding: 1%;
        }
      }

      .profileStatsLayout {
        width: 30%;
        .followStatus {
          display: flex;
          justify-content: space-evenly;
          align-items: center;

          .profileStats {
            width: 100%;
            list-style-type: none;
            margin-bottom: 40px;
            padding: 0px;
            display: flex;
            justify-content: space-evenly;
            align-items: center;

            li {
              width: 100px;
              display: flex;
              flex-direction: column;
              align-items: center;
              justify-content: center;

              .statsCount {
                margin-bottom: 10px;
                font-size: 18px;
              }
              .statsTitle {
                font-size: 16px;
              }
            }
          }

          .dmBtnLayout {
            width: 35%;
            margin-left: 30px;
            margin-bottom: 40px;

            .dmBtnSvg {
              cursor: pointer;
            }
            .dmButton {
              &:hover {
                fill: #ff0066;
              }
            }
          }

          .writeBtnLayout {
            width: 35%;
            margin-left: 30px;
            margin-bottom: 40px;

            .writeBtnSvg {
              cursor: pointer;
            }
            .writeButton {
              &:hover {
                fill: #ff0066;
              }
            }
          }
        }

        .followBtnLayout {
          width: 100%;
          display: flex;
          align-items: center;
          margin-left: 50px;
          margin-top: 0;

          .followButton {
            width: 150px;
            height: 40px;
            border: 1px solid black;
            border-radius: 20px;
            font-size: 18px;
            font-weight: 500;
            cursor: pointer;
            color: #ffffff;
            background-color: #000000;
          }
        }

        .editBtnLayout {
          width: 100%;
          display: flex;
          align-items: center;
          margin-left: 50px;
          margin-top: 0;

          .btn {
            width: 150px;
            height: 40px;
            border: 1px solid black;
            border-radius: 20px;
            font-size: 18px;
            font-weight: 500;
            background-color: #000000;
            cursor: pointer;
          }
          .editButton {
            color: #ffffff;
          }
          .followingBtn {
            color: #ffffff;
          }
          .followBtn {
            color: #000000;
            background-color: #ffffff;
          }
        }
      }
    }
    .feed {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 20px;
      margin-top: 20px;
    }
  }
}
